
#topFilm .topfilmList{
	/*display: flex;*/
	text-align: left;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	-ms-flex-pack:distribute;
	    justify-content:space-around;
	padding: 3%;
	margin-bottom: 66px;
}
#topFilm .topfilmList li {
	-webkit-box-flex:27%;
	    -ms-flex:27%;
	        flex:27%;
	-webkit-box-flex:0;
	    -ms-flex-positive:0;
	        flex-grow:0;
	text-align: center;
	margin-top: 16px;
	border: 1px solid #ddd;
	border-radius: 4px;
	padding-bottom:1%;
}
#topFilm .topfilmList li > div{
	height: 80%;
}
#topFilm .topfilmList li > div img{
	height: 100%;
}
#topFilm .topfilmList li h3{
	color: #333;
	font-size: 1.4rem;
	width: 100%;
	overflow: hidden;
    text-overflow: ellipsis;
    display: -webkit-box;
    -webkit-line-clamp: 1;
    -webkit-box-orient: vertical;
}
#topFilm .topfilmList p{
	font-size: 1.2rem;
}
#topFilm .topfilmList .average{
	font-size: 1.4rem;
	color: #f5a623;
}
#topFilm .down_arrow img{
	width: 24px;
	height: 24px;
	margin: 0 auto;
}
#topFilm .navBanner{
	transition: all 1s;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-orient:vertical;
	-webkit-box-direction:normal;
	    -ms-flex-direction:column;
	        flex-direction:column;
	position: absolute;
	top: 24px;
}
#topFilm .navBanner li{
	border-radius: 4px;
	margin-top: 4px;
	background-color:rgba(122,122,122,0.6);
	color:#fff;
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	width: 36px;
}
#topFilm .top_type{
	position: fixed;
    right: 4%;
    top: 22%;
    width: 36px;
    height: 32px;
}
#topFilm .top_type img{
	opacity: 0.8;
}



