
#app{
	margin-top: 0;
}
.film_box{
	width: 80%;
	margin: 1% auto 0 auto;
	border: 1px solid #ddd;
	border-radius: 4px;
	padding: 3% 3%;
}
.film_list{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
.film_box:last-child{
	margin-bottom: 66px;
}
.film_box .left{
	-webkit-box-flex:1.2;
	    -ms-flex:1.2;
	        flex:1.2;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
}
.film_box .left img{
	width: 100%;
	height: auto;
}
.film_box .right{
	padding-top: 1%;
	padding-left: 10px;
	-webkit-box-flex:3;
	    -ms-flex:3;
	        flex:3;
}
.film_box .right p{
	text-align: left;
	font-size: 1.4rem;
	color:#636563;
	margin-top: 2px;
}
.film_box .right .film_title a{
	color:#212021;
	font-size: 1.8rem;
}
.film_box .right .film_score span{
	color:#de4139;
	font-size: 1.6rem;
	margin-left: 2px;
}



