
footer{
	position: fixed;
	bottom: 0;
	width: 100%;
	border-top: 1px solid #ddd;
	background-color: #f7f7f7;
	z-index: 4;
}
footer ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
footer li{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	font-size: 1.5rem;
	position: relative;
	padding-top: 36px;
}
footer li.active a{
	color:#de4139;
}
footer .iconfont{
	font-size: 3rem;
	position: absolute;
	top: 0;
	left: 0;
	width: 100%;
}



