
.cinema_list{
	text-align: left;
	padding-left: 5%;
	margin-top: 16px;
	border-bottom: 1px solid #dedbde;
}
.cinema_list > div{
	margin: 4px 0;
}
.cinema_name{
	color: #313031;
	font-size: 2rem;
}
.cinema_price{
	color: #f74d42;
	font-size: 1.6rem;
}
.cinema_address{
	color:#636563;
	font-size: 1.6rem;
	padding-right: 2%;
}
.cinema_tel{
	color: #ff9a00;
	font-size: 1.8rem;
}
.cinema_bottom{
	position: relative;
	height: 24px;
}
.cinema_distance{
	color: #f74d42;
	font-size: 2rem;
	position: absolute;
	right: 10%;
}
#cinema_list .cinema_list:last-child{
	margin-bottom: 60px;
}



