
.adv_banner{
	margin-top: 48px;
}
.adv_banner img{
	height: 78px;
}
.select_city{
	font-size: 1.5rem;
	position: fixed;
	top: 48px;
	z-index:2;
	background-color: #fff;
	width: 100%;
	padding: 1% 0;
	border-bottom: 1px solid #ddd;
}
.select_city h4{	
	background-color:#f7f7f7;
	text-align: left;
	padding: 2% 0;
	padding-left: 2%;
	border-top:1px solid rgb(232,232,232);
	border-bottom: 1px solid  rgb(232,232,232);
}
.select_city p{
	text-align: left;
	font-size: 1.6rem;
	padding: 2% 0;
	padding-left: 2%;
}
.hitcity{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-ms-flex-wrap:wrap;
	    flex-wrap:wrap;
	font-size: 1.6rem;
	padding-top: 4px;
}
.hitcity span{
	-webkit-box-flex:20%;
	    -ms-flex:20%;
	        flex:20%;
	margin: 2px 2px;
	border: 1px solid rgb(232,232,232);
	border-radius: 4px;
}
.hitcity span:hover{
	background-color: #d63c31;
	color:#fffbff;
}



