
.header header{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	position: fixed;
	width: 100%;
	top: 0;
	background-color: #d63c31;
	height: 48px;
	font-size: 1.5rem;
	color:#fffbff;
	z-index: 3;
}
.header .left,.header .right{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	line-height: 48px;
}
.left span{
	margin-left: 4px;
	/*display: inline-block;*/
	display: inline-block;
	width: 8px;
	height: 8px;
	border-top: 2px solid #fff;
	border-left:2px solid #fff;
	-webkit-transform:rotate(-135deg);
	        transform:rotate(-135deg);
	position: relative;
	top:-2px;
}
.mid{
	-webkit-box-flex:2;
	    -ms-flex:2;
	        flex:2;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
}
.mid div{
	background-color: #b51c18;
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	height: 32px;
	line-height: 32px;
	border-radius: 4px;
	transition:all .5s;
}
.mid .active{
	background-color: #ffffff;
	color:#d6494a;
}



