
#rankbanner{
	padding: 1% 0;
	height: 36px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	vertical-align: center
}
#rankbanner select{
	position: relative;
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	padding: 3px 0 3px 1%;
	margin: 0 2px;
	width: 24%;
	text-align: center;
	border-radius: 4px;
	font-size: 1.5rem;
}
#rankbanner select:after{
	content:"";
	display: inline-block;
	border:1px solid black;
	width: 5px;
	height:5px;
}



