
.user_fav ul{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	padding: 4% 0;
	background-color: #fff;
}
.user_fav ul li{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	padding-top: 8%;
}
.user_fav ul li span{
	font-size: 1.2rem;
}
.user_fav .user_icon2{
	border-left: 1px solid #ddd;
	border-right: 1px solid #ddd;
}
.user_fav .user_icon3{
	background-size: 30% auto;
	background-position:center 0;
	border-right: 1px solid #ddd;
}



