
#user_page{
	position: inherit;
	background-color: #de4139;
	color:#fff;
}
.user{
	background-color: #f7f7f7;
}
.spacebox{
	height: 100px;
}
#user_page{
	height: 100px;
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
}
#user_page .user_left,#user_page .user_mid,#user_page .user_right{
	display: -webkit-box;
	display: -ms-flexbox;
	display: flex;
	-webkit-box-align:center;
	    -ms-flex-align:center;
	        align-items:center;
}
#user_page .user_left{
	-webkit-box-flex:1;
	    -ms-flex:1;
	        flex:1;
	padding-left: 5%;
	position: relative;
}
#user_page .user_left img{
	height: 75%;
	width: auto;
	position: absolute;
}
#user_page .user_mid{
	-webkit-box-flex:3;
	    -ms-flex:3;
	        flex:3;
	font-size: 1.6rem;
	margin-left: 4%;
}
#user_page .user_right{
	-webkit-box-flex:0.5;
	    -ms-flex:0.5;
	        flex:0.5;
}
#user_page .right_arrow{
	display: inline-block;
	width: 10px;
	height: 10px;
	border-top: 2px solid #fff;
	border-right: 2px solid #fff;
	-webkit-transform:rotate(45deg);
	        transform:rotate(45deg);
}
#user_order{
	margin-top: 20px;
	background-color: #fff;
}
#user_order .title{
	font-size: 1.6rem;
	padding: 2% 4%;
	border-bottom: 1px solid #e7e7e7;
}
#user_order .title span:first-child{
	float: left;
}
#user_order .title span:last-child{
	float: right;
	font-size: 1.4rem;
	color:#9c9a9c;
}
.user_mess:last-child ul li:last-child{
	margin-bottom: 60px;
}



