
#hit_filmlist > .hit_film:last-child{
		margin-bottom: 60px;
}
.hit_film{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		padding: 4px 4px 4px 4%;
		height: 155px;
		border-bottom: 1px solid #ddd;
		position: relative;
}
.hit_left{
		-webkit-box-flex:30%;
		    -ms-flex:30%;
		        flex:30%;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-align:center;
		    -ms-flex-align:center;
		        align-items:center;
}
.hit_right{
		-webkit-box-flex:70%;
		    -ms-flex:70%;
		        flex:70%;
		padding-left: 2%;
}
.hit_left img{
		height: 90%;
}
.hit_right p{
		text-align: left;
		margin-top: 5px;
}
.hit_right p:nth-child(1){
		margin-top: 8px;
		overflow: hidden;
	    text-overflow: ellipsis;
	    display: -webkit-box;
	    -webkit-line-clamp: 1;
	    -webkit-box-orient: vertical;
}
.hit_right .film_name{
		font-size: 1.8rem;
		font-weight: bold;
		color: #4a494aï¼›
}
.hit_right .film_avg span:first-child{
		color: #adaead;
		font-size: 1.4rem;
}
.hit_right .film_avg span:last-child{
		color: #ffb600;
		font-size: 1.8rem;
}
.hit_right .film_sum{
		font-size: 1rem;
		color: #7b7d7b
}
.hit_right .film_time{
		font-size: 1.4rem;		color: #7b7d7b
}
.hit_right .film_time span:last-child{
		font-size: 1.6rem;
		color: #ffb600;
		margin-left: 1%;
}
#buy_btn{
		display: inline-block;
		font-size: 1.5rem;
		border: 1px solid #f75940;
		color: #f75940;
		padding: 3px;
		width: 32px;
		position: absolute;
	    top: 22%;
    	right: 4%;
    	border-radius: 4px;
}



