
.search_banner{
	margin-top: 2px;
	position: relative;
}
.search_banner input{
	height: 32px;
	width: 100%;
	padding-left: 15%;
	border:none;
	border-radius: 4px;
}
.search_banner img{
    height: 24px;
    width: auto;
    position: absolute;
    top: 50%;
    left: 5%;
    margin-top: -12px;
}



