
#search_banner{
	background-color: #d63c31;
	padding: 12px 0;
	height: 65px;
}
#search_banner  input{
	width: 90%;
	height: 24px;
	border-radius: 4px;
}
#search_banner ul{
	margin-top: 10px;
}
#search_banner .search_btn{
	width: 25%;
	display: inline-block;
	background-color: #fff;
	border-radius: 4px;
	padding: 0 4px;
	font-size: 1.4rem;
}
#search_banner .search_btn:active{
	background-color:#ffb600;
}
#search_banner .search_btn .iconfont{
	font-size: 1.8rem;
	margin-right: 4px;
	color:#d63c31;
}



