
.load_gif{
	position: fixed;
	z-index: 5;
	top: 2px;
	left: 0;
	width: 100%;
	height: 100%;
	background-color: rgba(0,0,0,0.8)
}
.load_gif img{
	width: 60px;
	height: 60px;
	position: absolute;
	top:50%;
	left: 50%;
	margin-left: -30px;
	margin-top: -30px;
}



