
#film_detail header{
		height: 48px;
		background-color: #d63c31;
		line-height: 48px;
		text-align: center;
		position: relative;
}
#film_detail header span{
		color: #fff;
		font-size: 1.8rem;
}
#film_detail header a{
		position: absolute;
    	left: 5%;
}
#film_detail header a i{
		color: #fff;
    	font-size: 3.5rem;
}
	/*#film_detail header a img{
		height: 36px;
		width: auto;
	}*/
#film_detail .detail_box{
		left: 0;
		top: 48px;
		text-align: left;
		background-color: rgba(122,122,122,0.1);
		/*background-color: #fff;*/
		padding: 2% 4%;
		color: #000;
}
#film_detail .get_score{
		color: orange;
		font-size: 2rem;
		margin-left: 4px;
}
#film_detail .detail_box h3{
		font-size: 2rem;
}
#film_detail .detail_box h5{
		text-align: center;
}
#film_detail .detail_box p{
		font-size: 1.6rem;
}
#film_detail .detail_type span{
		color: #333	;
}
#film_detail .detail_box .detail_sum{
		margin-bottom: 4px;
}
#film_detail .detail_box .detail_sum h5{
		text-align: left;
		font-size: 1.6rem;
}
#film_detail .detail_box .detail_sum p{
		font-size: 1.4rem;
		text-indent: 2rem;
		/*color:#f26a1f;*/
}
#film_detail .body_list{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap:wrap;
		    flex-wrap:wrap;
		-ms-flex-pack:distribute;
		    justify-content:space-around;
}
#film_detail. .body_list > div{
		-ms-flex-preferred-size:30%;
		    flex-basis:30%;
		margin-top: 4%;
}
#film_detail .body_list > div img{
		height: 80%;
}
#film_detail .body_list > div h5{
		font-size: 1.6rem;
		color: #000;
}
#film_detail .body_page{
		top:48px;
		background-color: rgba(122,122,122,0.1);
		width: 100%;
		padding-bottom: 5%;
}
#film_detail .body_detail{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap:wrap;
		    flex-wrap:wrap;
}
#film_detail .body_detail{
		color: #ddd;
		padding-top: 16px;
}
#film_detail .body_detail .body_img{
		-webkit-box-flex:1;
		    -ms-flex:1;
		        flex:1;
}
#film_detail .body_detail .body_img img{
		width: 80%;
		height: auto;
		margin: 0 auto;
}
#film_detail .body_detail .body_info{
		-webkit-box-flex:2;
		    -ms-flex:2;
		        flex:2;
		text-align: left;
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-webkit-box-orient:vertical;
		-webkit-box-direction:normal;
		    -ms-flex-direction:column;
		        flex-direction:column;
		-webkit-box-pack:center;
		    -ms-flex-pack:center;
		        justify-content:center;
		color:#333;
}
#film_detail .body_detail .body_info h2{
		font-size: 2.4rem;
}
.body_detail .body_info h3{
		font-size: 1.6rem;
		margin-top: 8px;
}
#film_detail .body_detail .body_info a{
		font-size: 1.6rem;
		margin-top: 8px;
}
.body_page .body_film li{
		/*display: flex;*/
		height: 180px;
}
#film_detail .body_page .body_film li img{
		float: left;
		/*width: 120px;
		height: 180px;*/
		height: 80%;
		width: auto;
}
#film_detail .body_page .body_film li h3{
		float: left;
}
#film_detail .body_page >h3{
		color:#333;
		font-size: 2rem;
		margin-top: 12px;
		text-align: left;
		padding-left: 8px;
}
#film_detail .body_page .body_filmBox{
		display: -webkit-box;
		display: -ms-flexbox;
		display: flex;
		-ms-flex-wrap:wrap;
		    flex-wrap:wrap;
		-ms-flex-pack:distribute;
		    justify-content:space-around;
		-webkit-box-align:space-around;
		    -ms-flex-align:space-around;
		        align-items:space-around;
		color:#333;
}
#film_detail .body_page .body_filmBox img{
		width: 100%;
		height: 80%;
}
#film_detail .body_page .body_filmBox h4{
		font-size: 1.4rem;
}
#film_detail .body_page .body_filmBox .body_film{
		-webkit-box-flex:30%;
		    -ms-flex:30%;
		        flex:30%;
		-webkit-box-flex:0;
		    -ms-flex-positive:0;
		        flex-grow:0;
		margin-top: 16px;
}



